import React, { useState,useEffect } from 'react'
import RCTable from 'rc-table';
import axios from 'axios'
import './table.less'

export default function Table({title,columns,initialData =[],url,pagination}){
  const [data, setData] = useState(initialData)
  const [page, setPage] = useState(1)
  const [count, setCount] = useState()
  const [pageSize, setPageSize] = useState(10)

  const loadData = async (url,page) => {
    let restUrl = url;
    if(pagination){
      restUrl = `${restUrl}&page=${page}&page_amount=${pageSize}`
    }
    const res = await axios.get(restUrl);
    if(res.status === 200 && Array.isArray(res.data.data)){
      setCount(res.data.count / pageSize)
      if(res.data.count % pageSize > 0 ){
        setCount(Math.floor(res.data.count / pageSize) +1)
      }
      setData(res.data.data)
      setPage(page)
    }
  }

  const prePage = () => {
    if(page === 1){
      return;
    }
    loadData(url,page - 1)
  }
  const nextPage = () => {
    if(page === count){
      return;
    }

    loadData(url,page +1)
  }

  useEffect(() => {
    if(url){
      loadData(url,1);
    } 
  }, [url])
  return(
    <div className='table'>
      <div className='t-title'>{title}</div>
      <RCTable data={data} columns={columns} footer={() => pagination && <Pagination page={page} count={count} onPrePage={prePage} onNextPage={nextPage}/> }/>
    </div>
  )
}

function Pagination({page,count,onPrePage,onNextPage}){
  return (
    <div className='pagination'>
      <span className='arrow' onClick={onPrePage}> &lt; </span>
          <span>Page {page} of {count}</span><span className='arrow' onClick={onNextPage}> &gt; </span>
    </div>
  )
}